// Delete Experience
export const deleteExp = (id) => (dispatch) => {
    axios
        .delete(`/api/profile/experience/${id}`)
        .then(res => dispatch({
            type: 'GET_PROFILE',
            payload: res.data
        }))
        .catch(err => dispatch({
            type: GET_ERRORS,
            payload: err.response.data
        }))
}

